/* Header.css */
.header {
    position: relative;
  }
  
  .App-logo {
    height: 16vmin; /* Adjust size as needed */
    pointer-events: none;
    position: absolute;
    top: 10px;
    right: 10px;
  }
  
  .right-logo-link {
    position: absolute;
    top: 10px;
    left: 10px; /* Positioning the icon to the right */
  }
  
  .right-logo {
    height: 16vmin; /* Adjust size as needed */
    pointer-events: auto; /* Allow pointer events for clicking */
  }
  
